@media (max-width: 1200px) {
    .promo {
        &__header {
            font-size: 38px; }
        &__subheader {
            font-size: 22px; }
        &__icons {
            margin-top: 55px; }
        .button {
            &_main {
                margin-top: 60px; } } }
    .advantages {
        padding: 67px 0 0 0; } }
@media (max-width: 991px) {
    .promo {
        min-height: 610px;
        .header {
            justify-content: space-around;
            &__logo {
                max-width: 235px; }
            &__phone {
                font-size: 17px; } }
        .button {
            width: 145px;
            &_main {
                margin-top: 47px;
                width: 215px;
                font-size: 14px; } }
        &__wrapper {
            margin-top: 81px;
            padding-right: 25px; }
        &__header {
            font-size: 33px; }
        &__subheader {
            margin-top: 5px;
            font-size: 19px; }
        &__icons {
            margin-top: 43px; }
        &__icon {
            width: 124px;
            font-size: 13px;
            line-height: 17px; }
        &__link {
            margin-top: 17px;
            font-size: 14px;
            a {
                font-size: 14px; } } }
    .advantages {
        padding: 59px 0 0 0;
        .container {
            width: 90%; }
        &__header {
            font-size: 28px; }
        &__blocks {
            font-size: 17px; }
        &_descr {
            font-size: 13px; } } }
@media (max-width: 767px) {
    .promo {
        padding: 8px 0 0 0;
        .header {
            &__logo {
                max-width: 170px;
                margin-right: 2px;
                margin-left: 7px; }
            &__contacts {
                margin-right: 7px; }
            &__official {
                font-size: 10px;
                span {
                    font-size: 14px; } }
            &__phone {
                font-size: 15px;
                margin-bottom: 2px; } }
        .button {
            width: 120px;
            font-size: 11px;
            &_main {
                width: 150px;
                margin: 0 auto;
                margin-top: 70px; } }
        &__wrapper {
            padding: 0; }
        &__header {
            margin: 0 auto;
            font-size: 26px; }
        &__subheader {
            margin: 0 auto;
            margin-top: 5px;
            font-size: 14px; }
        &__icons {
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-top: 70px; }
        &__icon {
            width: 100px;
            font-size: 12px; }
        &__link {
            margin: 0 auto;
            margin-top: 15px;
            font-size: 15px;
            a {
                font-size: 13px; } } }
    .advantages {
        padding: 30px 0 0 0;
        &__header {
            font-size: 23px; }
        &__wrapper {
            padding: 50px 0 83px; }
        &__blocks {
            font-size: 14px; }
        &__descr {
            font-size: 12px; } } }
@media (max-width: 575px) {
    .header {
        &__official {
            display: none; } }
    .promo {
        &__header {
            font-size: 24px; }
        &__subheader {
            font-size: 12px; }
        &__icons {
            margin-top: 80px;
            width: 100%; } } }
