.promo {
	min-height: 650px;
	padding: 21px 0 93px 0;
	background: url('../img/main_bg.jpg') center (center/cover) no-repeat;
	&__wrapper {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		margin-top: 90px; }
	&__header {
		color: #FFF;
		font-size: 42px;
		font-weight: 700;
		text-transform: uppercase; }
	&__subheader {
		margin-top: 7px;
		color: #FFF;
		font-size: 24px;
		font-weight: 500;
		text-transform: uppercase; }
	&__icons {
		margin-top: 61px;
		width: 425px;
		display: flex;
		justify-content: space-between; }
	&__icon {
		width: 108px;
		color: #FFF;
		text-align: center;
		font-size: 14px;
		font-weight: 500;
		img {
			display: block;
			margin: 0 auto;
			margin-bottom: 20px; } }
	&__link {
		margin-top: 18px;
		color: #FFF;
		font-size: 16px;
		font-weight: 500;
		a {
			color: #FFF;
			font-size: 16px;
			font-weight: 500;
			text-transform: uppercase;
			text-decoration-line: underline; } } }
